<template>
  <div class="hello">
    <el-row :gutter="20" class="functionalArea">
      <el-col :span="6">
        <el-button-group>
          <el-button type="primary" icon="el-icon-plus" @click="addThemeFormVisible=true"></el-button>
          <el-button type="primary" icon="el-icon-share"></el-button>
          <el-button type="primary" icon="el-icon-delete" @click="clearThemesClick"></el-button>
        </el-button-group>
      </el-col>
      <el-col :span="6" :offset="12">
        <el-input placeholder="请输入内容" v-model="searchTheme" clearable>
          <template slot="append">
            <el-button type="success" plain>搜索</el-button>
          </template>
        </el-input>
      </el-col>
    </el-row>

    <el-table
            ref="multipleTable"
            :data="tableData"
            style="width: 100%"
            :header-cell-style="headerCellStyle"
            :cell-style="cellStyle"
            @select="handleSelectionChange"
            @select-all="selectAllFn"
    >
      <el-table-column
              type="selection"
              width="55">
      </el-table-column>
      <el-table-column
              label="序号"
              width="60">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.index }}</span>
        </template>
      </el-table-column>
      <el-table-column
              label="主题"
              width="180">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.name }}</span>
        </template>
      </el-table-column>
      <el-table-column
              label="代号"
              width="180">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.seq }}</span>
        </template>
      </el-table-column>
      <el-table-column
              label="服务区"
              width="180">
        <template slot-scope="scope">
          <el-popover trigger="hover" placement="top">
            <p>主题: {{ scope.row.name }}</p>
            <p>等待人数: {{ scope.row.wait }}</p>
            <div slot="reference" class="name-wrapper">
              <el-tag size="medium">{{ scope.row.area }}</el-tag>
            </div>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column
              label="操作"
              min-width="240">
        <template slot-scope="scope">
          <el-button
                  size="mini"
                  @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button
                  size="mini"
                  type="danger"
                  @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog title="添加主题" :visible.sync="addThemeFormVisible" width="500px">
      <el-form :model="form">
        <el-form-item label="主题名称" :label-width="formLabelWidth">
          <el-input v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="服务区" :label-width="formLabelWidth">
          <el-select v-model="form.area" placeholder="请选择服务区" style="width:100%;">
            <el-option v-for="(item, index) in areaList" :key="index" :label="item.label" :value="item.val"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="主题代号" :label-width="formLabelWidth">
          <el-input v-model="form.seq" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addThemeFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="addThemeSureFn">确 定</el-button>
      </div>
    </el-dialog>

    <el-dialog title="编辑主题" :visible.sync="fixThemeFormVisible" width="500px">
      <el-form :model="form1">
        <el-form-item label="主题名称" :label-width="formLabelWidth">
          <el-input v-model="form1.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="服务区" :label-width="formLabelWidth">
          <el-select v-model="form1.area" placeholder="请选择服务区" style="width:100%;">
            <el-option v-for="(item, index) in areaList" :key="index" :label="item.label" :value="item.val"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="主题代号" :label-width="formLabelWidth">
          <el-input v-model="form1.seq" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="fixThemeFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="fixThemeSureFn">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { baseUrl } from '@/api/unsplash/baseUrl'
export default {
  name: 'ATheme',
  data () {
    return {
      searchTheme: '',
      tableData: [],
      baseUrl: baseUrl,
      areaList: [
        {
          label: '投资项目',
          val: '投资项目'
        },
        {
          label: '社会事业',
          val: '社会事业'
        },
        {
          label: '工商登记',
          val: '工商登记'
        }
      ],

      addThemeFormVisible: false,
      formLabelWidth: '120px',
      form: {
        name: '',
        area: '投资项目',
        seq: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: ''
      },
      fixThemeFormVisible: false,
      form1: {
        name: '',
        area: '投资项目',
        seq: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: ''
      },

      selectThemeIds: []
    }
  },
  watch: {
  },
  created: function () {
    this.getThemeFn()
  },
  methods: {
    addThemeSureFn () {
      let temp = {
        name: this.form.name.replace(/(^\s*)|(\s*$)/g, ''),
        area: this.form.area.replace(/(^\s*)|(\s*$)/g, ''),
        seq: this.form.seq.replace(/(^\s*)|(\s*$)/g, ''),
        user_id: sessionStorage.user_id
      }
      if (temp.name != '' && temp.seq != '') {
        this.addThemeFn(temp)
      } else {
        this.$message({
          type: 'warning',
          message: '请补全信息!'
        })
      }
    },
    fixThemeSureFn () {
      let temp = {
        name: this.form1.name.replace(/(^\s*)|(\s*$)/g, ''),
        area: this.form1.area.replace(/(^\s*)|(\s*$)/g, ''),
        seq: this.form1.seq.replace(/(^\s*)|(\s*$)/g, ''),
        id: this.form1.id,
        user_id: sessionStorage.user_id
      }
      if (temp.name != '' && temp.seq != '') {
        this.fixThemeFn(temp)
      } else {
        this.$message({
          type: 'warning',
          message: '请补全信息!'
        })
      }
    },
    clearThemesClick () {
      if (this.selectThemeIds.length) {
        let params = {
          id_list: this.selectThemeIds,
          user_id: sessionStorage.user_id
        }
        this.deleteSingleFn(params)
      } else {
        this.$message({
          type: 'warning',
          message: '未选择主题!'
        })
      }
    },
    handleEdit (index, row) {
      console.log(index, row)
      this.fixThemeFormVisible = true
      this.form1.name = row.name
      this.form1.area = row.area
      this.form1.seq = row.seq
      this.form1.id = row.id
    },
    handleDelete (index, row) {
      console.log(row)
      this.$confirm('确定删除主题：' + row.name, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let params = {
          id_list: [row.id],
          user_id: sessionStorage.user_id
        }
        this.deleteSingleFn(params)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    async getThemeFn () {
      try {
        let params = {
        }
        let res = await this.$api.unsplash.getSubjects(params)
        console.log(res)
        if (res.errno == '0') {
          this.tableData = res.data.subjects
          for (let i = 0; i < this.tableData.length; i++) {
            this.tableData[i].index = i + 1
          }
        } else {
          this.$alert('服务器错误', '警告', {
            confirmButtonText: '确定',
            callback: action => {
            }
          })
        }
      } catch (e) {
        console.log(e)
      }
    },
    async addThemeFn (params) {
      try {
        let res = await this.$api.unsplash.postAddTheme(params)
        if (res.errno == '0') {
          this.addThemeFormVisible = false
          this.$message({
            type: 'success',
            message: '增加主题成功'
          })
          this.getThemeFn()
        } else {
          this.$message({
            type: 'warning',
            message: '增加主题失败'
          })
        }
      } catch (e) {
        console.log(e)
      }
    },
    async fixThemeFn (params) {
      try {
        let res = await this.$api.unsplash.postfixTheme(params)
        if (res.errno == '0') {
          this.fixThemeFormVisible = false
          this.$message({
            type: 'success',
            message: '修改主题成功'
          })
          this.getThemeFn()
        } else {
          this.$message({
            type: 'warning',
            message: '修改主题失败'
          })
        }
      } catch (e) {
        console.log(e)
      }
    },
    async deleteSingleFn (params) {
      try {
        let res = await this.$api.unsplash.postClearTheme(params)
        console.log(res)
        if (res.errno == '0') {
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
          this.selectThemeIds = []
          this.getThemeFn()
        } else {
          this.myAlert('服务器错误', '警告', this)
        }
      } catch (e) {
        console.log(e)
      }
    },
    handleSelectionChange (selection, row) { // 单击
      console.log(selection)
      this.selectThemeIds = []
      for (let i = 0; i < selection.length; i++) {
        this.selectThemeIds.push(selection[i].id)
      }
    },
    selectAllFn (selection) { // 全选
      console.log(selection)
      this.selectThemeIds = []
      for (let i = 0; i < selection.length; i++) {
        this.selectThemeIds.push(selection[i].id)
      }
    },
    headerCellStyle ({ row, column, rowIndex, columnIndex }) {
      if (rowIndex > -1) {
        return {
          'text-align': 'center'
        }
      }
    },
    cellStyle ({ row, column, rowIndex, columnIndex }) {
      if (columnIndex >= 0) {
        return {
          'text-align': 'center'
        }
      }
    },
    myAlert (txt, title, self) {
      self.$alert(txt, title, {
        confirmButtonText: '确定',
        callback: action => {
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  .functionalArea{
    height: 80px;
  }
  .headerCellStyle{
    text-align: center;
  }

</style>



